.App {
  text-align: center;
  font-family: "Arial Narrow", "Roboto", "Helvetica", sans-serif, Arial;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #2db44b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
#FAQ {
  text-align: center;
}

.App-link {
  color: #2db44b;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#button {
  /* background-color: #616161; */
  width: 100%;
  text-transform: capitalize;
  /* border-radius: 15px; */
  /* border: 1px solid #2AB34B; */
  color: #616161;
}
#button:hover {
  /* background-color: #616161; */
  color: #616161;
  /* border-color: #77BB85; */
}

/* this css is for the charts that were designed by sabbiti */

#whitediv {
  background-color: #fff;
}

#CHARTS {
  text-align: center;
  margin-bottom: 35px;
  color: #0d0d0d;
  font-size: 50px;
}
.mega {
  /* margin-bottom: 30px; */
  margin-top: 15px;
  border: solid;
  border-color: #d3d0d0;
  border-radius: 10px;
  margin-left: 40px;
  margin-right: 40px;
  /* background-color: #77BB85; */
}
.chartHeading {
  text-align: center;
  color: rgb(49, 51, 53);
}
.HR {
  width: "50%";
  border-color: #d3d0d0;
}
.charts {
  width: "100%";
}

.makeStyles-drawerPaper-6 {
  width: 300px;
  /* background-color: rgb(43, 47, 58); */
  background-color: #e15c26;
}
.map {
  height: 700px;
  width: 99%;
  margin-left: 0px;
  margin-top: 78px;
}
