    margin-top: 0px;
    width: 50%;
    height: 80%;
  } */

.MuiPaper-root {
  background-color: #fff;
  border: #77bb85 solid 2px;
  border-top: springgreen;
}

#feedback {
  margin-right: 5%;
  float: left;
  color: #007bff;
  cursor: pointer;
}
#faq {
  background-color: #e15c26;
}
#pic {
  width: 500px;
  height: 300px;
}
.MuiAppBar-colorPrimary {
  color: rgb(243, 230, 230);
  background-color: #2ab34b;
  border: solid #77bb85 1px;
  position: fixed;

  border-radius: 0.5em;
}

#indicatorText,#chartText {
  color: #616161;
  text-align: center;
  background-color: #d0d0d0;
  border-radius: 0.5em;
  padding: 0.3em;
  margin-top: 20px;
  margin-right: 10px;
  margin-left: 10px;
}
.Icons {
  color: #fff;
  /* display: inline; */
  position: relative;
  bottom: -4em;
  background-color: #d0d0d0;
  padding: 0.7em;
  border-radius: 0.5em;
  margin-right: 10px;
  margin-left: 10px;
}

#twitter {
  padding-right: 140px;
  color: blue;
  size: 50px;
}

#facebook {
  color: blue;
}
.MuiExpansionPanel-rounded {
  background-color: #2ab34b;
  color: #fff;
  margin: 2%;
}
.MuiExpansionPanel-rounded:first-child {
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
}
