#callout {
  border-radius: 10px;
  position: absolute;
  padding: 8px;
  cursor: pointer;
  z-index: 9999;
  display: block;
}

.callout-arrow {
  height: 0;
  width: 0;
  position: absolute;
  cursor: pointer;
}

.info {
  padding: 6px 8px;
  font: 14px/16px Arial, Helvetica, sans-serif;
  background: #d0d0d0;
  /* background: rgba(250, 243, 243, 0.603); */
  /* box-shadow: 0 0 15px rgba(0, 0, 0, 0.2); */
  border-color: black;
  border-radius: 5px;
  width: 150px;
  height: 50px;
}

.btn-link:link,
a:link {
  text-decoration: none;
}

path.leaflet-interactive {
  stroke: #959791;
  fill: #2ab34b;
}

.MuiCheckbox-colorSecondary.Mui-checked {
  color: black;
}
#contact-form {
  width: 400px;
  padding: 10px;
}
.btn-primary {
  color: #fff;
  background-color: #2ab34b;
  border-color: #2ab34b;
}
.btn-primary:hover {
  text-decoration: none;
}
.MuiSwitch-colorSecondary.Mui-checked {
  color: #2ab34b;
}
.MuiSwitch-track,
.MuiSwitch-colorSecondary.Mui-checked + .MuiSwitch-track {
  background-color: #2ab34b;
}
